export default {
	data() {
		return {
			user_phone: '',
			user_pwd: '',
			change_type: 'password'
		};
	},
	onShow() {
		uni.hideHomeButton()
	},
	methods: {
		clearAccount() {
			this.user_phone = ''
		},
		changeStatus() {
			this.change_type = this.change_type === 'password' ? 'text' : 'password'
		},
		sign_in() {
			if(this.user_phone.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入账号'
				})
				return
			}
			if(this.user_pwd.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				}) 
				return
			}
			uni.request({
				url: 'https://ciai.le-cx.com/index.php/api/nurse/login',
				data: {
					account: this.user_phone,
					password: this.user_pwd
				},
				success: res => {
					// console.log(res.data)
					if(res.data.code !== 1) {
						uni.showToast({
							icon: 'none',
							title: '登录失败，账号或者密码错误'
						})
					} else {
						uni.showToast({
							icon: 'success',
							title: res.data.msg,
							duration: 2000
						})
						// 保存userInfo到本地并进行登录跳转
						uni.setStorage({
							key: 'userInfo',
							data: res.data.data
						})
						uni.switchTab({
							url: '../workbench/workbench'
						})
					}
				}
			})
		}
	}
}
